.advantages {
  display: grid;
  margin-top: 70px;

  grid-column-gap: 100px;
  grid-template-columns: 1fr 350px;

  & .title {
    margin-bottom: 40px;
  }
}

.advantages__text {
  margin-bottom: 20px;

  @include main-text;

  & span {
    font-weight: 600;
    color: $basic-red;
  }
}

@media screen and (max-width: 991px) {
  .advantages {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (max-width: 767px) {
  .advantages {
    margin-top: 50px;
  }
}
