.effective {
  margin-top: 70px;

  & .title {
    text-align: center;
  }
}

.effective__list {
  @include reset-list;

  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.effective__item {
  display: flex;
}

.effective__item-number {
  align-self: center;
  width: 100px;
  height: 100px;
  margin-right: 20px;
  padding-top: 25px;

  font-weight: 700;
  font-size: 30px;
  text-align: center;
  color: $basic-red;

  background-repeat: no-repeat;
  background-position: center;
}

.effective__item-number--60 {
  background-image: url("../img/60.svg");
}

.effective__item-number--29 {
  background-image: url("../img/29.svg");
}

.effective__item-number--15 {
  background-image: url("../img/15.svg");
}

.effective__description {
  width: 80%;
  margin: 0 auto;
  margin-top: 80px;
  padding: 30px;

  background-color: $light-grey;
  border-radius: 5px;
}

.effective__description-text span {
  font-weight: 500;
  color: $basic-red;
}

@media screen and (max-width: 991px) {
  .effective__list {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .effective__item {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .effective__description {
    width: 100%;
  }
}

@media screen and (max-width: 767px) {
  .effective {
    margin-top: 50px;

    & .title br {
      display: none;
    }
  }

  .effective__list {
    margin-left: 80px;
    justify-content: flex-start;
  }
}

@media screen and (max-width: 500px) {
  .effective__list {
    margin-left: 0;
  }
}
