.testemonials {
  margin-top: 70px;
  padding: 70px 0;

  background-color: $light-grey;

  & .title {
    text-align: center;
  }
}

.carousel-inner {
  margin-top: 50px;
}

.carousel-item {
  position: relative;

  width: 80%;
  margin: 0 auto;
  padding: 40px;

  background-color: $basic-white;
  border-radius: 5px;
}

.testemonial--wrapper {
  display: flex;
  align-items: center;
}

.user-info {
  @include main-text;

  margin-left: 20px;

  & p {
    margin-bottom: 5px;
  }
}

.user-info__name {
  margin-bottom: 5px;

  font-weight: 600;
}

.testemonial-text {
  margin-top: 20px;
  margin-bottom: 0;
}

.prev-icon {
  position: absolute;
  top: 50%;
  left: 45px;

  width: 20px;
  height: 20px;

  background-image: url("../img/arrow-left.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.next-icon {
  position: absolute;
  top: 50%;
  right: 20px;

  width: 20px;
  height: 20px;

  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 767px) {
  .testemonials {
    margin-top: 50px;
    padding: 50px 0;
  }
}
