.ingredients {
  margin-top: 70px;

  & .title {
    text-align: center;
  }
}

.ingredients__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
}

.ingredients__item {
  width: 48%;
  margin-bottom: 40px;
  padding-left: 120px;

  background-repeat: no-repeat;
  background-position: top left;

  &:first-child {
    background-image: url("../img/plant.png");
  }

  &:nth-child(2) {
    background-image: url("../img/plant2.png");
  }

  &:nth-child(3) {
    background-image: url("../img/plant3.png");
  }

  &:nth-child(4) {
    background-image: url("../img/plant4.png");
  }

  &:nth-child(5) {
    background-image: url("../img/plant5.png");
  }
}

.ingredients__item-header {
  font-size: 18px;
}

.ingredients__item-subheader {
  @include main-text;
}

.ingredients__text {
  @include main-text;

  font-weight: 600;
  font-size: 18px;
  text-align: center;

  & span {
    color: $basic-red;
  }
}

@media screen and (max-width: 991px) {
  .ingredients__list {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .ingredients__item {
    width: 100%;
    padding-bottom: 10px;
  }
}

@media screen and (max-width: 767px) {
  .ingredients {
    margin-top: 50px;

    & .title br {
      display: none;
    }
  }
}
