.request {
  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-top: 80px;
  background-color: $light-grey;
  padding: 30px;

  & .form__title {
    padding: 0;

    background-color: transparent;
  }

  & .form-price {
    margin-top: 20px;
  }
}

.request__form {
  width: 300px;
  margin-right: 30px;
}

.request__info {
  margin-left: 30px;
}

@media screen and (max-width: 991px) {
  .request {
    flex-wrap: wrap;
    justify-content: space-around;

    &::after {
      display: none;
    }
  }

  .request__form {
    width: 400px;
    padding: 30px;
    background-color: $light-grey;
    margin-top: 40px;
  }
}

@media screen and (max-width: 767px) {
  .request__form {
    margin-right: 0;
  }

  .request__info {
    margin-left: 0;
  }

  .request {
    margin-top: 50px;

    &::after {
      display: none;
    }
  }

  .request__form {
    order: 3;
  }
}
