.reasons {
  margin-top: 70px;
  padding: 70px 0 40px;

  background-color: $light-grey;

  & .title {
    text-align: center;
  }
}

.reasons__list {
  @include reset-list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 50px;
}

.reasons__item {
  display: flex;
  flex-direction: column;
  width: 28%;
  margin-bottom: 30px;
  padding-top: 130px;

  text-align: center;

  background-repeat: no-repeat;
  background-position: center top;
}

.reasons__item-header {
  margin-bottom: 15px;

  font-weight: 600;
  font-size: 18px;
}

.reasons__item--angry {
  background-image: url("../img/angry.png");
}

.reasons__item--vessel {
  background-image: url("../img/veins.png");
}

.reasons__item--varicose {
  background-image: url("../img/varicos.png");
}

.reasons__item--sugar {
  background-image: url("../img/sugar.png");
}

.reasons__item--weight {
  background-image: url("../img/weight.png");
}

.reasons__item-subheader {
  @include main-text;

  & span {
    font-weight: 600;
    color: $basic-red;
  }
}

@media screen and (max-width: 991px) {
  .reasons__item {
    width: 48%;
  }
}

@media screen and (max-width: 767px) {
  .reasons__item {
    width: 90%;
  }

  .reasons {
    margin-top: 50px;
    padding: 50px 0 20px;

    & .title br {
      display: none;
    }
  }
}
