.expert {
  margin-top: 70px;
  padding: 70px 0 0;

  background-color: $light-grey;

  & .title {
    margin-bottom: 20px;
  }

  & img {
    margin-top: 40px;
  }
}

.expert--wrapper {
  display: grid;

  grid-column-gap: 50px;
  grid-template-columns: 400px 1fr;
}

.expert__text {
  @include main-text;

  & span {
    font-weight: 600;
    color: $basic-red;
  }
}

.expert__info {
  margin-top: 30px;
}

.expert__info-text {
  margin-bottom: 7px;

  font-weight: 600;
}

@media screen and (max-width: 991px) {
  .expert--wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .expert__img {
    order: 2;
  }

  .expert__description {
    order: 1;
  }
}

@media screen and (max-width: 767px) {
  .expert {
    margin-top: 50px;
    padding: 50px 0 0;

    & .title br {
      display: none;
    }
  }
}
