.main-screen {
  padding: 50px 0;

  background-color: $basic-white;
  background-image: url("../img/text.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.main-screen--wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.main-screen__title {
  margin-bottom: 20px;

  font-weight: 700;
  font-size: 35px;
  text-align: center;

  & span {
    color: $basic-red;
  }
}

.main-screen__subtitle {
  @include main-text;

  margin-bottom: 50px;

  font-size: 18px;
  text-align: center;
}

.main-screen__icons {
  @include reset-list;
  @include main-text;

  font-size: 16px;
}

.main-screen__icons-item {
  margin-bottom: 30px;
  padding-left: 60px;

  background-repeat: no-repeat;
  background-position: top left;

  &:last-child {
    margin-bottom: 0;
  }
}

.main-screen__icons-item--clock {
  background-image: url("../img/clock.svg");
}

.main-screen__icons-item--heart {
  background-image: url("../img/heart.svg");
}

.main-screen__icons-item--granny {
  background-image: url("../img/granny.svg");
}

.main-screen__icons-item--graphic {
  background-image: url("../img/graphic.svg");
}

.form {
  width: 350px;

  & form {
    padding: 20px;

    background-color: $light-grey;
    border-radius: 5px;
  }
}

.form__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;

  background-color: $dark-grey;
  border-radius: 5px;
}

.form__title-number {
  width: 50px;
  height: 50px;
  margin: 0;
  padding-top: 12px;

  font-weight: 700;
  text-align: center;
  color: $basic-white;

  background-color: $basic-red;
  border-radius: 100%;
}

.form__title-text {
  margin: 0;

  font-size: 14px;
  color: $special-black;
}

.form-price {
  display: flex;
  justify-content: space-between;
  margin-top: 0;

  text-align: center;
}

.form-price--text {
  color: $special-black;

  & span {
    font-weight: 600;
    font-size: 18px;
  }

  &:last-child span {
    color: $basic-red;
  }
}

.form__label {
  margin-top: 10px;

  font-size: 14px;
  color: $special-black;
}

.form__input {
  width: 100%;
  margin-top: 5px;
  margin-bottom: 10px;
  padding: 10px;

  font-size: 14px;

  border-radius: 5px;
  border: 1px solid $basic-grey;

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.form__btn {
  width: 100%;
  margin-top: 20px;
  padding: 10px;

  color: $basic-white;

  background-color: $basic-red;
  border: none;
  border-radius: 5px;

  &:hover {
    color: $basic-white;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

.form__text {
  margin-top: 20px;

  font-size: 14px;
  text-align: center;
}

@media screen and (max-width: 991px) {
  .main-screen--wrapper {
    justify-content: space-around;
  }

  .form {
    margin-top: 50px;
  }
}

@media screen and (max-width: 767px) {
  .main-screen__img {
    order: 1;
  }

  .main-screen__icons {
    order: 2;
    margin-top: 40px;
  }

  .main-screen__title {
    font-size: 30px;

    & br {
      display: none;
    }
  }
}

@media screen and (max-width: 500px) {
  .main-screen__title {
    font-size: 25px;
  }

  .main-screen__subtitle {
    margin-bottom: 0;
  }
}
