.symptoms {
  padding: 70px;

  background-color: $basic-grey;
}

.title {
  @include big-text;

  & span {
    color: $basic-red;
  }
}

.symptoms--wrapper {
  display: grid;
  margin-top: 50px;

  grid-template-columns: 1fr 1fr;
}

.symptoms__list {
  @include reset-list;

  display: grid;

  grid-column-gap: 100px;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 10px;
}

.symptoms__list-item {
  padding: 2px 0;
  padding-left: 50px;

  background-image: url("../img/attention.svg");
  background-repeat: no-repeat;
  background-position: left top;
}

.symptoms__header {
  margin-top: 30px;
  margin-bottom: 10px;

  font-weight: 700;
  font-size: 22px;
}

.symptoms__text {
  width: 420px;
  padding: 10px;

  background-color: $grey;
  border-radius: 5px;
}

@media screen and (max-width: 991px) {
  .symptoms--wrapper {
    display: grid;
    margin-top: 50px;

    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 767px) {
  .symptoms {
    padding: 50px;
  }

  .title {
    & span br {
      display: none;
    }
  }
}

@media screen and (max-width: 567px) {
  .symptoms__list {
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 50px;
    grid-row-gap: 20px;
  }
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 20px;

    & br {
      display: none;
    }
  }

  .symptoms__list {
    grid-template-columns: 1fr;
  }

  .symptoms__text {
    width: 100%;
  }
}
