.page-footer {
  padding: 10px 0;

  background-color: $gray;
  margin-top: 70px;
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & .header__phone {
    margin-top: 0;
  }
}

.page-footer__copyright {
  margin-bottom: 0;

  font-size: 14px;
  color: $basic-white;
}

.footer-phone__text {
  text-align: center;
  color: $basic-white;
}

@media screen and (max-width: 500px) {
  .footer {
    flex-direction: column;
    align-items: center;
  }

  .footer-phone {
    margin-top: 30px;
  }

  .page-footer__copyright {
    text-align: center;
  }

  .header__logo {
    display: block;
    margin: 0 auto;
  }
}
