.steps {
  padding: 70px 0;

  background-color: $light-grey;

  & .title {
    text-align: center;
  }
}

.steps__list {
  @include reset-list;
  @include main-text;

  display: grid;
  margin-top: 50px;

  text-align: center;

  grid-row-gap: 60px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.steps__item {
  padding-top: 70px;
  padding-bottom: 40px;

  background-repeat: no-repeat;
  background-position: top center, center bottom, center right;
}

.steps__item--envelope {
  background-image: url("../img/envelope.svg"), url("../img/one.svg"), url("../img/arrow1.svg");
  background-position: top center, center bottom, center right;
}

.steps__item--consult {
  background-image: url("../img/consult.svg"), url("../img/two.svg"), url("../img/arrow1.svg");
  background-position: top center, center bottom, center right;
}

.steps__item--analyze {
  background-image: url("../img/analyze.svg"), url("../img/three.svg"), url("../img/arrow1.svg");
  background-position: top center, center bottom, center right;
}

.steps__item--calendar {
  background-image: url("../img/calendar.svg"), url("../img/four.svg"), url("../img/arrow2.svg");
  background-position: top center, center bottom, bottom right;
}


.steps__item--result {
  background-image: url("../img/result.svg"), url("../img/eight.svg");
}

.steps__item--doctor {
  background-image: url("../img/doctor.svg"), url("../img/seven.svg"), url("../img/arrow3.svg");
  background-position: top center, center bottom, center left;
}

.steps__item--money {
  background-image: url("../img/money.svg"), url("../img/six.svg"), url("../img/arrow3.svg");
  background-position: top center, center bottom, center left;
}

.steps__item--delivery {
  background-image: url("../img/delivery.svg"), url("../img/five.svg"), url("../img/arrow3.svg");
  background-position: top center, center bottom, center left;
}

@media screen and (max-width: 991px) {
  .steps__list {
    display: flex;
    flex-wrap: wrap;
  }

  .steps__item {
    width: 48%;
  }

  .steps__item--envelope {
    order: 1;
    background-image: url("../img/envelope.svg"), url("../img/one.svg"), url("../img/arrow1.svg");
    background-position: top center, center bottom, center right;
  }

  .steps__item--consult {
    order: 2;
    background-image: url("../img/consult.svg"), url("../img/two.svg"), none;
    background-position: top center, center bottom, center right;
  }

  .steps__item--analyze {
    order: 3;
    background-image: url("../img/analyze.svg"), url("../img/three.svg"), url("../img/arrow1.svg");
    background-position: top center, center bottom, center right;
  }

  .steps__item--calendar {
    order: 4;
    background-image: url("../img/calendar.svg"), url("../img/four.svg"), none;
    background-position: top center, center bottom, bottom right;
  }


  .steps__item--result {
    order: 8;
    background-image: url("../img/result.svg"), url("../img/eight.svg"), none;
  }

  .steps__item--doctor {
    order: 7;
    background-image: url("../img/doctor.svg"), url("../img/seven.svg"), url("../img/arrow1.svg");
    background-position: top center, center bottom, center right;
  }

  .steps__item--money {
    order: 6;
    background-image: url("../img/money.svg"), url("../img/six.svg"), none;
    background-position: top center, center bottom, center left;
  }

  .steps__item--delivery {
    order: 5;
    background-image: url("../img/delivery.svg"), url("../img/five.svg"), url("../img/arrow1.svg");
    background-position: top center, center bottom, center right;
  }
}

@media screen and (max-width: 767px) {
  .steps {
    padding: 50px 0;
  }
}

@media screen and (max-width: 500px) {
  .steps__item {
    width: 100%;
  }

  .steps__item--envelope {
    order: 1;
    background-image: url("../img/envelope.svg"), url("../img/one.svg"), none;
    background-position: top center, center bottom, center right;
  }

  .steps__item--consult {
    order: 2;
    background-image: url("../img/consult.svg"), url("../img/two.svg"), none;
    background-position: top center, center bottom, center right;
  }

  .steps__item--analyze {
    order: 3;
    background-image: url("../img/analyze.svg"), url("../img/three.svg"), none;
    background-position: top center, center bottom, center right;
  }

  .steps__item--calendar {
    order: 4;
    background-image: url("../img/calendar.svg"), url("../img/four.svg"), none;
    background-position: top center, center bottom, bottom right;
  }


  .steps__item--result {
    order: 8;
    background-image: url("../img/result.svg"), url("../img/eight.svg"), none;
  }

  .steps__item--doctor {
    order: 7;
    background-image: url("../img/doctor.svg"), url("../img/seven.svg"), none;
    background-position: top center, center bottom, center right;
  }

  .steps__item--money {
    order: 6;
    background-image: url("../img/money.svg"), url("../img/six.svg"), none;
    background-position: top center, center bottom, center left;
  }

  .steps__item--delivery {
    order: 5;
    background-image: url("../img/delivery.svg"), url("../img/five.svg"), none;
    background-position: top center, center bottom, center right;
  }
}
