.page-header {
  padding: 10px 0;

  background-color: $grey;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__list {
  @include reset-list;
  @include main-text;

  display: flex;
  align-items: center;
}

.header__list-item {
  margin-right: 40px;
  padding-left: 50px;

  background-repeat: no-repeat;
  background-position: top left;

  &:last-child {
    margin-right: 0;
  }
}

.header__list-item--plant {
  background-image: url("../img/plant.svg");
}

.header__list-item--buyers {
  background-image: url("../img/buyers.svg");
}

.header-phone {
  text-align: center;
}

.header-phone__text {
  @include main-text;

  margin-top: 0;
  margin-bottom: 10px;
}

.header-phone__btn {
  padding: 10px 30px;

  font-weight: 500;
  color: $basic-white;
  text-decoration: none;

  background-color: $basic-red;
  background-image: url("../img/phone.svg");
  background-repeat: no-repeat;
  background-position: 14px center;
  border-radius: 5px;

  &:hover {
    color: $basic-white;

    opacity: 0.8;
  }

  &:focus {
    outline: none;
    opacity: 0.8;
  }
}

@media screen and (max-width: 991px) {
  .header__logo {
    width: 100px;
  }

  .page-header {
    padding: 20px 0;
  }

  .header__list-item {
    margin-right: 30px;
    padding-left: 45px;
    font-size: 14px;
  }

  .header-phone__text {
    font-size: 14px;
  }

  .header-phone__btn {
    font-size: 14px;
  }
}

@media screen and (max-width: 767px) {
  .header {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .header-phone {
    margin-top: 30px;
  }
}

@media screen and (max-width: 440px) {
  .header__logo {
    padding-bottom: 20px;
  }
}
